:root {
  --bg-white: hslhsl(0,0%,94.9%);
  --black: hsl(0, 0%, 0%);
  --btn-cyan: hsl(171, 66%, 44%);
  --btn-lightblue: hsl(233, 100%, 69%);
  --bg-gray:hsl(0,0%,59.2%);
  --darkgrayishblue: hsl(210, 10%, 33%);
  --p-grayishblue: hsl(201, 11%, 66%);
  --hli-col:hsl(210,9.5%,32.9%);
}

:root {
  --font-baijamjuree: "Bai Jamjuree", sans-serif ;
}

html {
  font-size: 62.8%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-baijamjuree);
}

p {
  font-size: 1.6rem;
  color: var(--p-grayishblue);
}

h1, h2, h3, li {
  color: var(--h-col);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}